// Helper functions for mock data
export const getTemplatesByCategory = (category) => {
  if (category === 'All') return mockTemplates;
  return mockTemplates.filter(template => template.category === category);
};

export const getFreeTemplates = () => {
  return mockTemplates.filter(template => !template.is_pro);
};

export const getProTemplates = () => {
  return mockTemplates.filter(template => template.is_pro);
};

export const getTemplateById = (id) => {
  return mockTemplates.find(template => template.id === parseInt(id));
};

export const getBlogPostBySlug = (slug) => {
  return mockBlogPosts.find(post => post.slug === slug);
};

export const searchTemplates = (searchTerm) => {
  const term = searchTerm.toLowerCase();
  return mockTemplates.filter(template => 
    template.title.toLowerCase().includes(term) ||
    template.description.toLowerCase().includes(term) ||
    template.category.toLowerCase().includes(term)
  );
};

export const getCategories = () => {
  const categories = [...new Set(mockTemplates.map(template => template.category))];
  return ['All', ...categories];
};

export const getTags = () => {
  const allTags = mockBlogPosts.flatMap(post => post.tags);
  return [...new Set(allTags)];
};